<script>
import { mapGetters } from 'vuex'
import { imgFormatter } from '@/utils/index'
import { moneyToFixed } from '@/filters/index'

export default {
    name: 'vip',
    components: {
    },

    data() {
        return {
            currentSelectIndex: 1,
            vipInfo: null,
        }
    },

    computed: {
		app() { return this.$store.getters.app },
		user() { return this.$store.getters.user },
        product() { return this.$store.getters.product },
    },

    mounted() {
        this.$store.dispatch('getConfig', 'vip').then(list => {
            this.vipInfo = list;
            this.currentSelectIndex = this.user.userInfo.level;
        });
    },

    methods: {
        
        getPercentage(a, b) {
            if(b == 0) {
                return 100;
            }

            const p = Number(a)/Number(b);

            if(p > 1) {
                return 100;
            }

            return moneyToFixed(p * 100);
        },
    }
}
</script>

